<template>
    <div class="box">
        <div class="box-body">
            <div class="col">123</div>
            <div class="col">123</div>
            <div class="col">123</div>
            <div class="col">123</div>
            <div class="col">123</div>
        </div>
    </div>
</template>

<script>
import axios from "axios";

export default {
    metaInfo: {
        title: "Chỉnh sửa header"
    },
    data() {
        return {
            api: this.$store.state.domainApi,
            banner: []
        }
    },
    methods: {
        getDataBanner(){
            axios.get(this.api + "/admin/banner",{
                headers:{
                    Authorization: `Bearer ${localStorage.token}`
                }
            })
            .then(response=>{
                this.banner = response.data;
            })
        },
        updateStatus(index){
            var item = this.banner[index];
            var status = item.status == 0 ? 1 : 0;
            axios.put(this.api + "/admin/banner",{
                id: item.id,
                status: status
            },{
                headers:{
                    Authorization: `Bearer ${localStorage.token}`
                }
            })
            .then(response=>{
                if(response.data.success) this.banner[index].status = status;
                else this.flash(response.data.message, "error", {timeout: 3000});
            });
        },
        deleteBanner(index){
            this.$swal.fire({
                title: 'Bạn có muốn xóa banner này không',
                text: "",
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                cancelButtonText: 'Không !',
                confirmButtonText: 'Có !'
            }).then((result) => {
                if (result.value) {
                    axios.delete(`${this.api}/admin/banner`,{
                        headers: {
                            Authorization: `Bearer ${localStorage.token}`
                        },
                        params:{
                            id: this.banner[index].id
                        }
                    })
                    .then(response => {
                        if(response.data.success===1){
                            this.banner.splice(index, 1);
                            this.flash(response.data.message, 'success', {
                                timeout: 3000
                            });
                        }else{
                            this.flash(response.data.message, 'error', {
                                timeout: 3000
                            });
                        }
                    })
                    .catch(e => {
                        console.log(e);
                    });
                }
            });
        }
    },
    created() {
        this.getDataBanner();   
    }
}
</script>

